/* Doi 
  - for Doi item
*/
.doiContainer {
    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
    padding: 6px 12px;
    margin: 12px 0;
}

/* anchor & its Visited States  */

.doiContainer a:link,
.doiContainer a:visited {
    color: #009acd;
}

/* Hover State */

.doiContainer a:hover {
    color: #006400;
}
